.toggle input[type='checkbox'] {
    display: none;
}

.toggle label {
    background-color: #869E9B;
    border: 2px solid #798F8C;
    border-radius: 50px;
    cursor: pointer;
    display: inline-block;
    position: relative;
    transition: all ease-in-out 0.3s;
    width: 65px;
    height: 37px;
}

.toggle label::after {
    background-color: #CBD3D8;
    border-radius: 50%;
    content: ' ';
    cursor: pointer;
    display: inline-block;
    position: absolute;
    left: 2px;
    top: 2px;
    transition: all ease-in-out 0.3s;
    width: 29px;
    height: 29px;
}

.toggle input[type='checkbox']:checked ~ label {
    background-color: #CBD3D8;
    border-color: #869E9B;
}

.toggle input[type='checkbox']:checked ~ label::after {
    background-color: #798F8C;
    -webkit-transform: translateX(29px);
            transform: translateX(29px);
}
